import React, { Component } from 'react'
import {
    CCardBody,
    CSwitch,
    CCollapse
} from '@coreui/react'


import CIcon from '@coreui/icons-react'
import "grapesjs/src/styles/scss/main.scss";
import 'grapesjs/dist/css/grapes.min.css';
import Cropper from "react-cropper";
import "cropperjs/dist/cropper.css";
import './New.css'

import 'react-image-crop/dist/ReactCrop.css';
import 'react-image-crop/lib/ReactCrop.scss';
import Axios from '../../../lib/api';
import swal from 'sweetalert';

import './ss.css';
// var $ = require('jquery')

const REACT_APP_BASE_URL = require('../../../lib/env').default;

var CONFIG = {
    'basic': {
        max_wizard: 6
    },
    'basic_plus': {
        max_wizard: 9
    },
    'coming_soon': {
        max_wizard: 8
    },

}
var IMAGE_LIST = 'imageList';
var FOOTER_LIST = 'footerList';

class Widget extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showFacebookSetting: false,
            showPoweredBySetting: false,
            showCallButtonSetting: false,
            showWhatsappSetting: false,

            facebook_enable: false,
            powered_by_enable: false,
            call_button_enable: false,
            whatsapp_enable: false,

            marginTop: 0,
            marginBottom: 0,

            paddingTop: 14,
            paddingBottom: 7,
            paddingLeft: 7,
            paddingRight: 7,
            height: 50,

            align: 'center',
            maxWidth: 600,

            showColorSetting: false,
            showPaddingSetting: false,
            showLogoSetting: true,

            background: '#000000',
            color: '#ffffff',
            opacity: 1,

            company: 'Powered By Master LPS',


            wizard_step: 2,

            isLoading: false,
            fontFamily: 'BlinkMacSystemFont',
            logoSrc: null,
            logoWidth: 20,//%
            logoHeight: 100,//%

            logoPosition: 'left',
            logoPositionX: 'initial',
            logoPositionY: 'initial',

            logoCroppedImageUrl: 'https://img1.wsimg.com/isteam/ip/27dcd564-67cc-49d8-88f5-ecbd1ee7feb2/84278061_1335685009970771_8929805060758568960_.jpg/:/',
            logoCrop: {
                unit: '%', // default, can be 'px' or '%'
                width: 100,
                height: 100
            },


            //call button
            phone_number: '4418642583168',
            effect: 'pulse',

            //whatsapp
            whatsapp_title: 'Chat with us on WhatsApp!',
            whatsapp_popup_message: 'Hello, how can we help you?',
            whatsapp_position: 'right',

            //facebook
            pageId: '103830958271014'

        }
        this.imageList = [];//ref list
        this.footerList = [];//ref list

        this.imageList_cropper = [];
        this.blob_imageList = [];
        this.blob_footerList = [];

        this.ref = React.createRef();

        this.powerdByHtmlRef = React.createRef();


        this.whatsappHtmlRef = React.createRef();
        this.whatsappJavascriptRef = React.createRef();

        this.callButtonHTMLRef = React.createRef();
        this.callButtonCSSRef = React.createRef();

        this.facebookHTMLRef = React.createRef();
        this.facebookJavascriptRef = React.createRef();



        this.widgetList = []

        this.logo_croper = null;
    }

    updateState(value, column) {
        return new Promise(r => {
            console.log(value, column)
            this.setState({ [column]: value }, () => {
                this._change();
                r();
            })
        })
    }

    getData() {
        var html = '';
        var js = '';
        var css = '';
        var externel_links = [];
        var widget_blob_list = [];
        if (this.state.powered_by_enable) {
            html += this.powerdByHtmlRef.current.innerHTML;
            if (this.logo_blob != null) widget_blob_list.push({ blob: this.logo_blob, column: 'logoCroppedImageUrl' })
        }

        if (this.state.whatsapp_enable) {
            html += this.whatsappHtmlRef.current.innerHTML;
            js += this.whatsappJavascriptRef.current.innerHTML;


            externel_links = externel_links.concat([
                '<script src="https://code.jquery.com/jquery-3.5.1.min.js" integrity="sha256-9/aliU8dGd2tb6OSsuzixeV4y/faTqgFtohetphbbj0=" crossorigin="anonymous"></script>',
                '<link rel="stylesheet" href="https://rawcdn.githack.com/rafaelbotazini/floating-whatsapp/3d18b26d5c7d430a1ab0b664f8ca6b69014aed68/floating-wpp.min.css">',
                '<script type="text/javascript" src="https://rawcdn.githack.com/rafaelbotazini/floating-whatsapp/3d18b26d5c7d430a1ab0b664f8ca6b69014aed68/floating-wpp.min.js"></script>'
            ]);
        }
        if (this.state.call_button_enable) {
            html += this.callButtonHTMLRef.current.innerHTML;;
            css += this.callButtonCSSRef.current.innerHTML;
        }
        if (this.state.facebook_enable) {
            html += this.facebookHTMLRef.current.innerHTML;
            js += this.facebookJavascriptRef.current.innerHTML;
            externel_links = externel_links.concat([
                '<script src="https://code.jquery.com/jquery-3.5.1.min.js" integrity="sha256-9/aliU8dGd2tb6OSsuzixeV4y/faTqgFtohetphbbj0=" crossorigin="anonymous"></script>'
            ]);
        }

        var r = {
            // facebook_enable: this.state.facebook_enable,
            // whatsapp_enable: this.state.whatsapp_enable,
            // powered_by_enable: this.state.powered_by_enable,
            // call_button_enable: this.state.call_button_enable,

            html: html,
            js: js,
            css: css,
            externel_links: externel_links,
            widget_blob_list: widget_blob_list
        }
        return r;
    }

    _change() {
        var r = this.getData();
        this.props.updated(r);
    }

    onLogoChanged(value, column) {
        this.setState({ [column]: value })
    }

    readFile(event) {
        const reader = new FileReader();
        reader.addEventListener('load', () => this.onLogoChanged(reader.result, 'logoSrc'));
        reader.readAsDataURL(event.target.files[0]);
    }

    initWhatsapp() {
        var $ = window.$;
        console.error($('#WAButton'))
        $('#WAButton').html('')
        $('#WAButton').floatingWhatsApp({
            phone: this.state.phone_number, //WhatsApp Business phone number International format-
            //Get it with Toky at https://toky.co/en/features/whatsapp.
            headerTitle: this.state.whatsapp_title, //Popup Title
            popupMessage: this.state.whatsapp_popup_message, //Popup Message
            showPopup: true, //Enables popup display
            buttonImage: '<img src="https://rawcdn.githack.com/rafaelbotazini/floating-whatsapp/3d18b26d5c7d430a1ab0b664f8ca6b69014aed68/whatsapp.svg" />', //Button Image
            //headerColor: 'crimson', //Custom header color
            //backgroundColor: 'crimson', //Custom background button color
            position: this.state.whatsapp_position
        });
    }

    componentDidMount = () => {
        Axios.get(`admin/widget/widgets`).then(r => {
            if (r && r.data) {
                var v = r.data
                // if (typeof v.options === 'string') v.options = JSON.parse(v.options)
                this.widgetList = r.data;

                var w = this.widgetList.find(w => w.title === 'whatsapp');
                if (w) {
                    if (typeof w.options == 'string') w.options = JSON.parse(w.options);

                    this.setState({
                        whatsapp_title: w.options.whatsapp_title, whatsapp_popup_message: w.options.whatsapp_popup_message, whatsapp_position: w.options.whatsapp_position,
                        phone_number: w.options.phone_number
                    })
                    this.initWhatsapp();
                }


            }
        })

        if (this.pageId)
            Axios.get(`admin/page/pages?id=${this.pageId}`).then(r => {
                this.setState({ page: r.data[0], title: r.data[0].title, description: r.data[0].description, keywords: r.data[0].keywords, url: r.data[0].url, facebook_pixel: r.data[0].facebook_pixel, google_analytics: r.data[0].google_analytics })
                // this.editor.setComponents(r.data[0].html)
                // this.editor.setStyle(r.data[0].css)
            })

        // Axios.get('storage/storage-list').then(r => {
        //     const am = this.editor.AssetManager;
        //     am.add(r.data);
        // })
    }

    async _save() {

        this.state.isLoading = true;
        if (this.callImage) {
            var formData = new FormData();

            formData.append("file", this.callImage);
            var callImageRes = await Axios.post(`${REACT_APP_BASE_URL}storage/upload`, formData, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            });
            this.setState({ callImage: callImageRes.data[0].src });
        }
        if (this.callButtonImage) {
            formData = new FormData();

            formData.append("file", this.callButtonImage);
            var callButtonImageRes = await Axios.post(`${REACT_APP_BASE_URL}storage/upload`, formData, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            });
            this.setState({ callButtonImageRes: callButtonImageRes.data[0].src });
        }


        var promise1 = this.blob_imageList.map(b => {
            var formData = new FormData();

            formData.append("file", b);
            return Axios.post(`${REACT_APP_BASE_URL}storage/upload`, formData, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            })
        })


        var promise2 = this.blob_footerList.map(b => {
            var formData = new FormData();

            formData.append("file", b);
            return Axios.post(`${REACT_APP_BASE_URL}storage/upload`, formData, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            })
        })

        var r = await Promise.all(promise1);
        this.blob_imageList = [];
        r.forEach((r, index) => {
            this.onImageListChanged(IMAGE_LIST, index, r.data[0].src, 'croppedImageUrl');
        });

        r = await Promise.all(promise2);
        this.blob_footerList = [];
        r.forEach((r, index) => {
            this.onImageListChanged(FOOTER_LIST, index, r.data[0].src, 'croppedImageUrl');
        });

        var html = this.ref.current.innerHTML;

        var { url, title, page, description, keywords } = this.state;
        var w = this.widgetList.find(w => w.title === this.state.selected_widget);
        var externel_links = [];
        var css = ''
        var js = '';
        if (w) {
            if (typeof w.externel_links == 'string') w.externel_links = JSON.parse(w.externel_links);
            if (w.externel_links) externel_links = w.externel_links;
            css = w.css;
            js = w.js;
        }
        Axios.post('admin/page/add-page', { id: page ? page.id : null, url: url, html: html, css: css, js: js, title: title, description: description, keywords: keywords, lead_id: this.lead_id, facebook_pixel: this.state.facebook_pixel, google_analytics: this.state.google_analytics, externel_links: externel_links })
            .then(r => {
                // this.props.history.goBack();
                this.props.history.push(`/new-builder/${r.data.id}`)
            })
            .catch(e => console.error(e))
    }
    async onSave() {
        if (this.state.url === null || this.state.url.length === 0) {
            swal({
                title: "Input page url",
                // text: "Check your email",
                icon: "error",
                buttons: {
                    confirm: {
                        text: "OK",
                        value: true,
                        visible: true,
                        className: "",
                        closeModal: true
                    },
                }
            });
            this.setState({ wizard_step: 2 })
            return
        }

        var { url, page } = this.state;

        Axios.post('admin/page/check-page-url', { id: page ? page.id : null, url: url })
            .then(r => {
                if (r.data.data == null) return this._save();

                swal({
                    title: "Please input other url",
                    // text: "Check your email",
                    icon: "error",
                    buttons: {
                        confirm: {
                            text: "OK",
                            value: true,
                            visible: true,
                            className: "",
                            closeModal: true
                        },
                    }
                });
                this.setState({ wizard_step: 2 })
            })
            .catch(e => {

                swal({
                    title: "Error occur, Please input other url",
                    // text: "Check your email",
                    icon: "error",
                    buttons: {
                        confirm: {
                            text: "OK",
                            value: true,
                            visible: true,
                            className: "",
                            closeModal: true
                        },
                    }
                });
                this.setState({ wizard_step: 2 })
            })
    }

    valueChange(e, column) {
        var value = e.target.type === 'checkbox' ? e.target.checked : e.target.value;
        this.setState({ [column]: value }, () => {
            this._change()
        });

    }

    render() {
        return (<>
            <div className="border d-flex justify-content-between  pl-3 pr-3 pt-2 pb-2">
                <CSwitch className={'mx-1'} color={'primary'} defaultChecked={false} onChange={(e) => { this.valueChange(e, 'facebook_enable'); }} />
                Facebook Chat
                <CIcon name="cil-scrubber" className="float-right" onClick={() => { this.setState({ showFacebookSetting: !this.state.showFacebookSetting }) }} />
            </div>
            <CCollapse show={this.state.showFacebookSetting}>
                <CCardBody>
                    <div className="form-group mt-3">
                        <label className="form-label" htmlFor="exampleRadios2">Page Id</label>
                        <input type="text" value={this.state.pageId} className="form-control" onChange={(e) => { this.valueChange(e, 'pageId'); }} />
                    </div>
                </CCardBody>
                <CCardBody>
                    <div ref={this.facebookHTMLRef}>
                        <div id="fb-root"></div>
                        <div className="fb-customerchat" attribution='setup_tool' page_id={this.state.pageId}></div>
                    </div>
                    <pre ref={this.facebookJavascriptRef} style={{ display: 'none' }}>
                        {
                            `
                               window.fbAsyncInit = function () {
                                FB.init({
                                    xfbml: true,
                                    version: 'v9.0'
                                });
                            };
                    
                            (function (d, s, id) {
                                var js, fjs = d.getElementsByTagName(s)[0];
                                if (d.getElementById(id)) return;
                                js = d.createElement(s);
                                js.id = id;
                                js.src = 'https://connect.facebook.net/en_US/sdk/xfbml.customerchat.js';
                                fjs.parentNode.insertBefore(js, fjs);
                            }(document, 'script', 'facebook-jssdk'));
                            `
                        }
                    </pre>
                </CCardBody>
            </CCollapse>
            <div className="border d-flex justify-content-between  pl-3 pr-3 pt-2 pb-2 mt-2">
                <CSwitch className={'mx-1'} color={'primary'} defaultChecked={false} onChange={(e) => this.valueChange(e, 'call_button_enable')} />
                Call Button
                <CIcon name="cil-scrubber" className="float-right" onClick={() => { this.setState({ showCallButtonSetting: !this.state.showCallButtonSetting }) }} />
            </div>
            <CCollapse show={this.state.showCallButtonSetting}>
                <CCardBody>
                    <div className="form-group mt-3">
                        <label className="form-label" htmlFor="exampleRadios2">Phone Number</label>
                        <input type="text" value={this.state.phone_number} className="form-control" placeholder="phone number" onChange={(e) => {
                            this.valueChange(e, 'phone_number');
                        }} />
                    </div>

                    <div className="form-group mt-31">
                        <label className="form-label" htmlFor="exampleRadios2">Effect</label>
                        <select value={this.state.effect} className="form-control" onChange={(e) => { this.valueChange(e, 'effect'); }}>
                            <option value="pulse">Pulse</option>
                            <option value="rotate">Rotate</option>
                            <option value="">None</option>
                        </select>
                    </div>
                </CCardBody>
                <CCardBody>
                    <div className="border pb-5" ref={this.callButtonHTMLRef} style={{ minHeight: '300px', paddingTop: '60px' }}>
                        <div className="phone-no-images" style={{ width: '100%', maxWidth: '140px', textAlign: 'center', margin: 'auto', marginTop: '20px' }}>
                            <a href={'tel:' + this.state.phone_number}>
                                <div style={{ position: 'relative', height: '140px' }} className={this.state.effect}>
                                    <svg style={{ position: 'absolute', top: '0px', left: '0px' }} enableBackground="new 0 0 140 140" height="140px" id="Layer_1" version="1.1" viewBox="0 0 40 40" width="140px" space="preserve" xmlns="http://www.w3.org/2000/svg" xlink="http://www.w3.org/1999/xlink">
                                        <g>
                                            <g>
                                                <g>
                                                    <g>
                                                        <path fill="#4DCC00" d="M20,2c9.925,0,18,8.075,18,18s-8.075,18-18,18c-9.925,0-18-8.075-18-18S10.075,2,20,2 M20,0 C8.955,0,0,8.954,0,20c0,11.047,8.955,20,20,20c11.047,0,20-8.953,20-20C40,8.954,31.047,0,20,0L20,0z" />
                                                    </g>
                                                </g>
                                            </g>
                                            <path fill="#4DCC00" fillRule="evenodd" clipRule="evenodd" d="M14.371,9.793c1.207-0.228,1.998,1.133,2.6,2.072   c0.586,0.912,1.307,1.982,1.016,3.169c-0.162,0.666-0.764,1.029-1.219,1.422c-0.449,0.388-1.133,0.744-1.299,1.34   c-0.271,0.967,0.322,1.982,0.689,2.56c0.834,1.306,1.842,2.483,3.129,3.534c0.623,0.51,1.488,1.191,2.355,1.016   c1.295-0.262,1.637-1.859,3.047-2.072c1.342-0.203,2.25,0.77,3.008,1.422c0.73,0.631,1.908,1.439,1.828,2.52   c-0.047,0.621-0.545,1.006-0.977,1.381c-0.439,0.383-0.824,0.813-1.258,1.096c-1.051,0.686-2.34,1.022-3.82,0.976   c-1.451-0.045-2.607-0.538-3.656-1.097c-2.051-1.094-3.672-2.633-5.199-4.348c-1.502-1.686-2.889-3.682-3.656-5.889   c-0.957-2.756-0.451-5.587,1.098-7.353c0.262-0.3,0.676-0.613,1.055-0.935C13.49,10.284,13.84,9.893,14.371,9.793z" />
                                        </g>
                                    </svg>
                                    <div className={'element '}></div>
                                </div>
                            </a>
                        </div>

                    </div>
                    {/* Used for save css */}
                    <pre ref={this.callButtonCSSRef} style={{ display: 'none' }}>
                        {
                            `
        .element {
            height: 140px;
            width: 140px;
            position: absolute;
            left: 0;
            top: 0;
            margin: 0 auto;
            border-radius: 50%;
          }
          
          .element:before,
          .element:after {
            content: '';
            display: block;
            position: absolute;
            border: 50%;
            border: 1px solid #4DCC00;
            left: -0px;
            right: -0px;
            top: -0px;
            bottom: -0px;
            border-radius: 50%;
            opacity: 0;
            backface-visibility: hidden;
          }
          
          
          .pulse .element:before,
          .pulse .element:after {
            animation: pulse 1.5s linear infinite;
          }
          
          .pulse .element:after {
            animation-duration: 0.5s;
          }
          
          @keyframes pulse {
            0% {
              transform: scale(1.6);
              opacity: 0;
            }
          
            50% {
              opacity: 1;
            }
          
            100% {
              transform: scale(1.2);
              opacity: 0;
            }
          }
          
          .rotate {
            animation: rotate 4s linear infinite;
          }
          
          @keyframes rotate {
            from {
              transform: rotate(0deg);
            }
          
            to {
              transform: rotate(359deg);
            }   
          }
            
    `
                        }
                    </pre>
                </CCardBody>
            </CCollapse>
            <div className="border d-flex justify-content-between  pl-3 pr-3 pt-2 pb-2 mt-2">
                <CSwitch className={'mx-1'} color={'primary'} defaultChecked={false} onChange={(e) => this.valueChange(e, 'whatsapp_enable')} />
                Whatsapp
                <CIcon name="cil-scrubber" className="float-right" onClick={() => { this.setState({ showWhatsappSetting: !this.state.showWhatsappSetting }) }} />
            </div>
            <CCollapse show={this.state.showWhatsappSetting}>
                <CCardBody>
                    <div className="form-group mt-3">
                        <label className="form-label" htmlFor="exampleRadios2">Phone Number</label>
                        <input type="text" value={this.state.phone_number} className="form-control" placeholder="phone number" onChange={(e) => {
                            this.valueChange(e, 'phone_number');
                            this.initWhatsapp();
                        }} />
                    </div>
                    <div className="form-group mt-3">
                        <label className="form-label" htmlFor="exampleRadios2">Title</label>
                        <input type="text" value={this.state.whatsapp_title} className="form-control" placeholder="Title" onChange={(e) => {
                            this.valueChange(e, 'whatsapp_title');
                            this.initWhatsapp();
                        }} />
                    </div>
                    <div className="form-group mt-3">
                        <label className="form-label" htmlFor="exampleRadios2">Popup Message</label>
                        <input type="text" value={this.state.whatsapp_popup_message} className="form-control" placeholder="Title" onChange={(e) => {
                            this.valueChange(e, 'whatsapp_popup_message');
                            this.initWhatsapp();
                        }} />
                    </div>
                    <div className="form-group mt-31">
                        <label className="form-label" htmlFor="exampleRadios2">Position</label>
                        <select value={this.state.whatsapp_position} className="form-control" onChange={(e) => {
                            this.valueChange(e, 'whatsapp_position');
                            this.initWhatsapp();
                        }}>
                            <option value="left">Left</option>
                            <option value="right">Right</option>
                        </select>
                    </div>
                </CCardBody>
                <CCardBody>
                    <div ref={this.whatsappHtmlRef} >
                        <div id="WAButton"></div>
                    </div>
                    <pre ref={this.whatsappJavascriptRef} style={{ display: 'none' }}>
                        {
                            `
                                    $('#WAButton').html('')
                                    $('#WAButton').floatingWhatsApp({
                                        phone: "${this.state.phone_number}",
                                        headerTitle: "${this.state.whatsapp_title}",
                                        popupMessage: "${this.state.whatsapp_popup_message}",
                                        showPopup: true,
                                        buttonImage: '<img src="https://rawcdn.githack.com/rafaelbotazini/floating-whatsapp/3d18b26d5c7d430a1ab0b664f8ca6b69014aed68/whatsapp.svg" />',
                                        //headerColor: 'crimson', //Custom header color
                                        //backgroundColor: 'crimson', //Custom background button color
                                        position: "${this.state.whatsapp_position}"
                                    });
                                    `
                        }
                    </pre>
                </CCardBody>
            </CCollapse>
            <div className="border d-flex justify-content-between  pl-3 pr-3 pt-2 pb-2 mt-2">
                <CSwitch className={'mx-1'} color={'primary'} defaultChecked={false} onChange={(e) => this.valueChange(e, 'powered_by_enable')} />
                Powered By
                <CIcon name="cil-scrubber" className="float-right" onClick={() => { this.setState({ showPoweredBySetting: !this.state.showPoweredBySetting }) }} />
            </div>

            <CCollapse show={this.state.showPoweredBySetting}>
                <CCardBody>
                    <div className="form-group mt-31">
                        <label className="form-label">Logo</label>
                        <input className="form-control-file" type="file" accept="image/*"
                            onChange={(event) => this.readFile(event)}
                            onClick={(event) => event.target.value = null} />
                    </div>

                    {this.state.logoSrc && <Cropper
                        // style={{ height: 400, width: "100%" }}
                        // initialAspectRatio={1}

                        src={this.state.logoSrc}
                        viewMode={1}
                        guides={false}
                        minCropBoxHeight={10}
                        minCropBoxWidth={10}
                        background={false}
                        responsive={true}
                        autoCropArea={1}
                        checkOrientation={false} // https://github.com/fengyuanchen/cropperjs/issues/671
                        onInitialized={(instance) => { this.logo_croper = instance; }}
                        ready={() => {
                            this.onLogoChanged(this.logo_croper.getCroppedCanvas().toDataURL(), 'logoCroppedImageUrl');
                            this.logo_croper.getCroppedCanvas().toBlob((blob) => { this.logo_blob = blob; this._change() })
                        }}
                        cropend={() => {
                            this.onLogoChanged(this.logo_croper.getCroppedCanvas().toDataURL(), 'logoCroppedImageUrl');
                            this.logo_croper.getCroppedCanvas().toBlob((blob) => { this.logo_blob = blob; this._change() })
                        }}
                    />
                    }

                    <div className="form-group mt-3">
                        <label className="form-label" htmlFor="exampleRadios2">Logo Size</label>
                        <input type="number" value={this.state.logoWidth} className="form-control" min="0" max="100" onChange={(e) => { this.valueChange(e, 'logoWidth'); }} />
                        <input type="number" value={this.state.logoHeight} className="form-control mt-2" min="0" max="100" onChange={(e) => { this.valueChange(e, 'logoHeight'); }} />
                    </div>

                    <div className="form-group mt-31">
                        <label className="form-label" htmlFor="exampleRadios2">Position</label>
                        <select value={this.state.logoPosition} className="form-control" onChange={(e) => this.valueChange(e, 'logoPosition')}>
                            <option>left</option>
                            <option>right</option>
                            <option>bottom</option>
                            <option>center</option>
                            <option>top</option>
                        </select>
                    </div>
                    <div className="form-group mt-3">
                        <label className="form-label" htmlFor="exampleRadios2">Position (X, Y)</label>
                        <input type="number" value={this.state.logoPositionX} className="form-control" onChange={(e) => { this.valueChange(e, 'logoPositionX'); }} />
                        <input type="number" value={this.state.logoPositionY} className="form-control mt-2" onChange={(e) => { this.valueChange(e, 'logoPositionY'); }} />
                    </div>
                </CCardBody>
                <CCardBody>
                    <div ref={this.powerdByHtmlRef} >
                        <div style={{
                            background: this.state.background, opacity: this.state.opacity,
                            color: this.state.color, textAlign: this.state.align, fontFamily: this.state.fontFamily,
                            margin: 'auto', marginTop: this.state.marginTop + 'px', marginBottom: this.state.marginBottom + 'px',
                            paddingTop: this.state.paddingTop + 'px',
                            paddingBottom: this.state.paddingBottom + 'px',
                            paddingLeft: this.state.paddingLeft + 'px',
                            paddingRight: this.state.paddingRight + 'px',
                            height: this.state.height + 'px',
                            backgroundImage: "url(" + this.state.logoCroppedImageUrl + ")",
                            backgroundSize: `${this.state.logoWidth}% ${this.state.logoHeight}%`,
                            backgroundRepeat: 'no-repeat',
                            backgroundPosition: this.state.logoPosition,
                            backgroundPositionX: this.state.logoPositionX + 'px',
                            backgroundPositionY: this.state.logoPositionY + 'px',
                        }}>
                            <span style={{}}>{this.state.company}</span>
                        </div>
                    </div>
                </CCardBody>
            </CCollapse>
        </>
        )
    }
}

export default Widget
